.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #007bff 0%, #6a11cb 100%);
}

.login-form {
  background: #fff;
  padding: 40px 32px;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.10);
  display: flex;
  flex-direction: column;
  width: 320px;
}

.login-form h2 {
  margin-bottom: 24px;
  color: #007bff;
  text-align: center;
}

.login-form input {
  margin-bottom: 16px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
}

.login-form button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 12px;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.2s;
}

.login-form button:hover {
  background: #0056b3;
}

.login-error {
  color: #d32f2f;
  margin-bottom: 12px;
  text-align: center;
}
